.footer {
    display: flex;
    align-items: center;
    background-color: rgb(83, 155, 128);
    width: 100%; height: 20rem;
    bottom: 0;
    padding: 0 2rem;
}

.footer-nav ul {
    
}
.footer-nav ul li {
    list-style: none;
    padding: 0.5rem 1rem;
}
.footer-nav ul li a {
    color: azure;
    text-decoration: none;
}

.footer .info-contact {
    width: 50%; height: 100%;
    display: flex;
    padding: 2rem;
}
.footer .info-contact .map{
    width: 100%;height: 100%;
    background-color: azure;
}