.nav {
    width: 60%; height: 100%;
}
.nav ul {
    width: 100%; height: 100%;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.nav ul li {
    list-style: none;
}
.nav ul li a {
    text-decoration: none;
    color: rgb(13, 14, 13);
    font-size: 18px;
}